body {
	padding: 0 15px; }

.container-new {
	max-width: 1200px;
	width: 100%; }

header {
	min-width: 290px;
	overflow: hidden;
	@include md {
		min-width: 290px;
		height: 400px; }
	.bg-img {
		@include md {
			height: 400px; }
		@include sm {
			height: 290px; }
		@include r(450) {
			height: 200px; } }
	.container-new {
		@include md {
			height: 400px; }
		@include sm {
			height: 290px; }
		@include r(450) {
			height: 200px; } }
	.container-new, .owl-carousel, .owl-carousel .owl-stage-outer {
		@include md {
			height: 400px; }
		@include sm {
			height: 290px; }
		@include r(450) {
			height: 200px; } }
	@include sm {
		height: 290px; }
	@include r(450) {
		height: 200px; } }

.navbar-collapse {
	@include md {
		text-align: center;
		// width: 100%
		position: absolute;
		right: -200px;
		top: 90px;
		padding: 20px 30px;
		transition: transform 0.5s;
		opacity: 0;
		&.is-open {
			transform: translateX(-200px);
			opacity: 1; } } }
.navbar-brand {
	@include md {
		margin-left: 15px; } }

.pull-left, .pull-right {
	@include md {
		float: none !important; } }

.nav-right {
	@include md {
		text-align: center; } }

.navbar-nav {
	@include md {
		margin: 0;
		padding-top: 0;
		h1 {
			display: none; } }
	@include md {
		li {
			float: none;
			a {
				margin: 0; }
			span {
				display: none; } } } }

.nav-right {
	@include md {
		margin-right: 0;
		display: flex;
		justify-content: center;
		flex-direction: column-reverse; } }

.menu-mobile-cross {
	display: none;
	@include md {
		display: inline-block;
		position: absolute;
		top: 28px;
		right: 15px;
		width: 33px;
		height: 33px;
		&.is-open {
			&:before {
				transform: rotate(-45deg);
				top: 10px;
				background-color: #fff; }
			&:after {
				top: 10px;
				transform: rotate(45deg);
				background-color: #fff; }

			span {
				top: 10px;
				opacity: 0;
				top: 0;
				background-color: #fff; } }
		span {
			position: absolute;
			top: 10px;
			width: 33px;
			height: 3px;
			margin-bottom: 10px;
			background-color: #fff;
			cursor: pointer;
			transition: opacity 0.3s;

			&.is-open {
				transform: rotate(-45deg);
				background-color: #fff; } }
		&:before {
			content: '';
			position: absolute;
			top: 0;
			width: 33px;
			height: 3px;
			margin-bottom: 10px;
			background-color: #fff;
			cursor: pointer;
			transition: transform 0.5s;
			.is-open & {
				transform: rotate(45deg);
				top: 10px;
				background-color: #fff; } }
		&:after {
			content: '';
			position: absolute;
			top: 21px;
			width: 33px;
			height: 3px;
			margin-bottom: 10px;
			background-color: #fff;
			cursor: pointer;
			transition: transform 0.5s;
			.is-open & {
				opacity: 0;
				top: 0;
				background-color: #fff; } } } }

.navbar {
	margin: 0 -15px; }

.owl-carousel .owl-item img {
	@include r(1200) {
		object-fit: cover;
		object-position: 50% 50%;
		height: 500px; }
	@include md {
		height: 400px; }
	@include sm {
		height: 290px; }
	@include r(450) {
		height: 200px; } }
