body {
  padding: 0 15px;
}

.container-new {
  max-width: 1200px;
  width: 100%;
}

header {
  min-width: 290px;
  overflow: hidden;
}

@media only screen and (max-width: 1023px) {
  header {
    min-width: 290px;
    height: 400px;
  }
}

@media only screen and (max-width: 1023px) {
  header .bg-img {
    height: 400px;
  }
}

@media only screen and (max-width: 767px) {
  header .bg-img {
    height: 290px;
  }
}

@media only screen and (max-width: 450px) {
  header .bg-img {
    height: 200px;
  }
}

@media only screen and (max-width: 1023px) {
  header .container-new {
    height: 400px;
  }
}

@media only screen and (max-width: 767px) {
  header .container-new {
    height: 290px;
  }
}

@media only screen and (max-width: 450px) {
  header .container-new {
    height: 200px;
  }
}

@media only screen and (max-width: 1023px) {
  header .container-new, header .owl-carousel, header .owl-carousel .owl-stage-outer {
    height: 400px;
  }
}

@media only screen and (max-width: 767px) {
  header .container-new, header .owl-carousel, header .owl-carousel .owl-stage-outer {
    height: 290px;
  }
}

@media only screen and (max-width: 450px) {
  header .container-new, header .owl-carousel, header .owl-carousel .owl-stage-outer {
    height: 200px;
  }
}

@media only screen and (max-width: 767px) {
  header {
    height: 290px;
  }
}

@media only screen and (max-width: 450px) {
  header {
    height: 200px;
  }
}

@media only screen and (max-width: 1023px) {
  .navbar-collapse {
    text-align: center;
    position: absolute;
    right: -200px;
    top: 90px;
    padding: 20px 30px;
    transition: transform 0.5s;
    opacity: 0;
  }
  .navbar-collapse.is-open {
    transform: translateX(-200px);
    opacity: 1;
  }
}

@media only screen and (max-width: 1023px) {
  .navbar-brand {
    margin-left: 15px;
  }
}

@media only screen and (max-width: 1023px) {
  .pull-left, .pull-right {
    float: none !important;
  }
}

@media only screen and (max-width: 1023px) {
  .nav-right {
    text-align: center;
  }
}

@media only screen and (max-width: 1023px) {
  .navbar-nav {
    margin: 0;
    padding-top: 0;
  }
  .navbar-nav h1 {
    display: none;
  }
}

@media only screen and (max-width: 1023px) {
  .navbar-nav li {
    float: none;
  }
  .navbar-nav li a {
    margin: 0;
  }
  .navbar-nav li span {
    display: none;
  }
}

@media only screen and (max-width: 1023px) {
  .nav-right {
    margin-right: 0;
    display: flex;
    justify-content: center;
    flex-direction: column-reverse;
  }
}

.menu-mobile-cross {
  display: none;
}

@media only screen and (max-width: 1023px) {
  .menu-mobile-cross {
    display: inline-block;
    position: absolute;
    top: 28px;
    right: 15px;
    width: 33px;
    height: 33px;
  }
  .menu-mobile-cross.is-open:before {
    transform: rotate(-45deg);
    top: 10px;
    background-color: #fff;
  }
  .menu-mobile-cross.is-open:after {
    top: 10px;
    transform: rotate(45deg);
    background-color: #fff;
  }
  .menu-mobile-cross.is-open span {
    top: 10px;
    opacity: 0;
    top: 0;
    background-color: #fff;
  }
  .menu-mobile-cross span {
    position: absolute;
    top: 10px;
    width: 33px;
    height: 3px;
    margin-bottom: 10px;
    background-color: #fff;
    cursor: pointer;
    transition: opacity 0.3s;
  }
  .menu-mobile-cross span.is-open {
    transform: rotate(-45deg);
    background-color: #fff;
  }
  .menu-mobile-cross:before {
    content: '';
    position: absolute;
    top: 0;
    width: 33px;
    height: 3px;
    margin-bottom: 10px;
    background-color: #fff;
    cursor: pointer;
    transition: transform 0.5s;
  }
  .is-open .menu-mobile-cross:before {
    transform: rotate(45deg);
    top: 10px;
    background-color: #fff;
  }
  .menu-mobile-cross:after {
    content: '';
    position: absolute;
    top: 21px;
    width: 33px;
    height: 3px;
    margin-bottom: 10px;
    background-color: #fff;
    cursor: pointer;
    transition: transform 0.5s;
  }
  .is-open .menu-mobile-cross:after {
    opacity: 0;
    top: 0;
    background-color: #fff;
  }
}

.navbar {
  margin: 0 -15px;
}

@media only screen and (max-width: 1200px) {
  .owl-carousel .owl-item img {
    object-fit: cover;
    object-position: 50% 50%;
    height: 500px;
  }
}

@media only screen and (max-width: 1023px) {
  .owl-carousel .owl-item img {
    height: 400px;
  }
}

@media only screen and (max-width: 767px) {
  .owl-carousel .owl-item img {
    height: 290px;
  }
}

@media only screen and (max-width: 450px) {
  .owl-carousel .owl-item img {
    height: 200px;
  }
}

.movie-block {
  height: 100%;
  margin: 0 21px;
}

@media only screen and (max-width: 1023px) {
  .movie-block:last-child {
    margin-right: 21px;
  }
}

@media only screen and (max-width: 1023px) {
  .movie-block:first-child {
    margin-left: 21px;
  }
}

.movie-block p {
  margin-bottom: 30px;
}

@media only screen and (max-width: 767px) {
  .container-p {
    padding: 0;
  }
}
